.news {
  bottom: 0;
  -webkit-box-shadow: 0px -5px 5px 0px rgba(1, 1, 1, 0.4);
          box-shadow: 0px -5px 5px 0px rgba(1, 1, 1, 0.4);
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  width: 100%;
}

.title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: center;
      justify-content: center;
  padding: .5em;
}

.title > div {
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 160%;
  font-weight: 900;
  text-align: center;
}

.text {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 4 1;
      flex: 4 1;
  padding: .5em 2em .5em 4em;
}

.text > div {
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 120%;
  font-weight: 500;
}

.enter {
  left: -100%;
  -webkit-transition: left .5s ease;
  -o-transition: left .5s ease;
  transition: left .5s ease;
}

.enter.enterActive {
  left: 0;
}

.leave {
  left: 0;
  -webkit-transition: left .5s ease;
  -o-transition: left .5s ease;
  transition: left .5s ease;
}

.leave.leaveActive {
  left: 100%;
}


